import React from 'react';
import Feed from '../components/Feed';
import CreatePost from '../components/CreatePost';
import EventCalendar from '../components/EventCalendar';
import EventForm from '../components/EventForm';
import SpeakingClub from '../components/SpeakingClub';

const Community = () => {
  const userId = "demo_user_id"; //  remplacer par l'ID rel de l'utilisateur connect
  const eventId = null; // Peut tre dfini dynamiquement selon le contexte
  const isAdmin = true; //  adapter selon le rle utilisateur

  return (
    <div>
      <h2>Communaut</h2>
      <CreatePost userId={userId} />
      <Feed userId={userId} />
      <EventCalendar />
      {isAdmin && <EventForm />}
      {eventId && <SpeakingClub eventId={eventId} />}
    </div>
  );
};

export default Community;
